import {NgModule}     from '@angular/core';

import {RouterModule} from '@angular/router';

import {ElectionListComponent} from "./election-list/election-list.component";


/**
 * Clase ElectionRoutingModule. Implementa la funcionalidad de un Mdulo.
 *
 * ElectionRoutingModule es la clase encarga de redistribuir el trfico principal
 * de la aplicacin dentro del mdulo Election.
 */
@NgModule({
    imports: [
        RouterModule.forChild([
            {
                path: '',
                component: ElectionListComponent
            },
            {
                path: ':id',
                component: ElectionListComponent
            },
        ])
    ],
    exports: [
        RouterModule
    ]
})
export class ElectionRoutingModule {


    /**
     * Constructor de la clase.
     */
    constructor() {
    }
}



