import {Component, OnInit} from '@angular/core';
import {Election} from "../../../../../dao/model/election";
import {Router} from "@angular/router";
import {MdDialogRef} from "@angular/material";
import {Party} from "../../../../../dao/model/party";
import {DialogComponent} from "../../../../shared/dialog/dialog-component";
import {Observable, BehaviorSubject} from "rxjs";
import {DaoService} from "../../../../../dao/dao.service";
import {District} from "../../../../../dao/model/district";
import {AppList} from "../../../../../dao/shared/app-list";


/**
 * Clase ElectionDetailComponent. Implementa la funcionalidad de un Componente.
 *
 * ElectionDetailComponent se encarga de representar visualmente una eleccin.
 */
@Component({
  selector: 'app-election-detail',
  templateUrl: './election-detail.component.html',
  styleUrls: ['./election-detail.component.css']
})
export class ElectionDetailComponent implements DialogComponent, OnInit {


  private editing: boolean;
  private isFullScreen: boolean;
  private resizableSubscriber:BehaviorSubject<boolean>;


  /**
   * Atributo model.
   *
   * El tipo es string.
   */
  private _model: Election;
  private _id: string;


  /**
   *
   * @type {"../../Observable".Observable<T>}
   * @private
   */
  private _onResize;


    /**
     * Constructor de la clase.
     */
    constructor(private dialogRef: MdDialogRef<ElectionDetailComponent>,
                private daoService: DaoService,
                private router: Router) {
      this.resizableSubscriber = new BehaviorSubject<boolean>(false);
      this.onResize = this.resizableSubscriber.asObservable();
    }


    /**
     * Getter del atributo id.
     * (Necesario por la interfaz ComponentWithParams)
     *
     * @returns {Election}
     */
    get id(): string {
        return this._id;
    }


    /**
     * Setter del atributo id
     * (Necesario por la interfaz ComponentWithParams)
     *
     * @param value
     */
    set id(value: string) {
        this._id = value;
    }


  /**
   * Getter del atributo onResize.
   * (necesario por la interfaz DialogComponent)
   *
   * @returns {Observable<boolean>}
   */
  get onResize(): Observable<boolean> {
    return this._onResize;
  }


  /**
   * Setter del atributo onResize.
   * (necesario por la interfaz DialogComponent)
   *
   * @param value
   */
  set onResize(value: Observable<boolean>) {
    this._onResize = value;
  }


    /**
     * Getter del atributo election.
     *
     * Se ha creado para facilitar la comprensin del cdigo refiriendose
     * directamente como un partido y no como un modelo.
     *
     * @returns {Party}
     */
    get election(): Election {
        return this._model;
    }


    /**
     * Setter del atributo election.
     *
     * Se ha creado para facilitar la comprensin del cdigo refiriendose
     * directamente como un partido y no como un modelo.
     *
     * @returns {Party}
     */
    set election(value: Election) {
        this._model = value;
    }


  ngOnInit(): void {
    this.election = Election.newInstance();
    if (this.id) {
      this.daoService.getElectionObjectObservable(this.id)
        .subscribe(election => {
          this.election = election;
        });
    } else {
      this.editing = true;
    }
  }


  get districtList(): AppList<District> {
    return this.election.districtList;
  }

  get partyList(): AppList<Party> {
    return this.election.partyList;
  }

    /**
     * Funcin closeDialog.
     *
     * Es la encargada de cerrar el dilogo.
     */
    private closeDialog(): void {
        this.dialogRef.close();
    }

    private navigateToParty(id: string): void {
        this.closeDialog();
        this.router.navigate(['/app/parties', id]);
    }

  private addParty(id: string): void {
    this.daoService.addPartyToElection(this.election.id, id);
  }

  private removeParty(id: string): void {
    this.daoService.removePartyFromElection(this.election.id, id);
  }


  private addDistrict(regionId: string): void {
    this.daoService.addDistrictToElection(this.election.id, regionId);
  }



  private removeDistrict(districtId: string): void {
    this.daoService.deleteDistrict(districtId);
  }


    /**
     * Funcin fabIcon.
     *
     * Es la encargada de manejar el control del icono que se muestra en el
     * botn de edicin.
     *
     * @returns {string}
     */
    private fabIcon(): string {
        if (this.editing) {
            return 'done';
        } else {
            return 'edit';
        }
    }

    private get iconScreenChange(){
      if(this.isFullScreen){
        return 'fullscreen_exit';
      } else {
        return 'fullscreen';
      }
    }

  private screenStateChange() {
    this.isFullScreen = !this.isFullScreen;
    this.resizableSubscriber.next(this.isFullScreen);
  }

    /**
     * Funcin editingChange.
     *
     * Es la funcin encargada de cambiar el estado de edicin a visualizacin
     * y guardar los datos si fuera necesario.
     */
    private editingChange(): void {
        if (this.editing) {
            this.saveChanges();
        }
        this.editing = !this.editing;
    }


  /**
   * Funcin saveChanges.
   *
   * Es la encargada de guardar los datos despus de una modificacin o creacin.
   */
  private saveChanges(): void {
    this.daoService.saveElection(this.election).then(() => {
      this.id = this.election.id;
    }).catch(reason => {
      console.error(reason.message);
    });
  }



  /**
   * Funcin delete.
   *
   * Es la encargada de eliminar la eleccin de la persistencia de la aplicacin.
   */
  private delete(): void {
    this.daoService.deleteElection(this.election).then(() => {
      this.closeDialog();
    }).catch(reason => {
      console.error(reason.message);
    });
  }
}



