import {Injectable} from '@angular/core';
import {ActivatedRoute, Params} from "@angular/router";
import {Model} from "../../../dao/model/model";
import {DaoService} from "../../../dao/dao.service";

import 'rxjs/add/operator/toPromise';
import {ObjectFromRoute} from "./object-from-route";

/**
 * Clase RouteService. Implementa la funcionalidad de un Servicio.
 *
 * Este servicio es el encargado de comprobar si se est hacediendo a la ruta
 * de un modelo concreto. En el caso de ser as, enva una llamada mediante la
 * interfaz ObjectFromRoute con objeto de dicho modelo.
 */
@Injectable()
export class RouterService {


    /**
     * Atributo route.
     *
     * El tipo es ActivatedRoute.
     *
     */
    private route: ActivatedRoute;


    /**
     * Constructor de la clase.
     *
     */
    constructor() {
    }


    /**
     * Funcin init.
     *
     * Inicializa los atributos de la clase. Puesto que es un servicio (no se
     * puede asignar atributos desde el constructor que no sean otros servicios
     * genricos) hay que hacerlo desde una funcin externa.
     *
     * @param route ruta desde la que se extrae el id del objeto a mostrar en
     * el dialog.
     */
    public init(route: ActivatedRoute) {
        this.route = route;
    }


    /**
     * Funcin getModelFromRoute.
     *
     * Se encarga de "leer" los parmetros de la url y en el caso de que estos
     * existan realiza una llamada a la funcin openDialog, que muestra el
     * detalle de una eleccion.
     */
    public readRoute(response: ObjectFromRoute) {

        this.route.params
            .forEach((params: Params) => {
                let id = params['id'];
                if ( id !== undefined) {
                    if (id === 'new' ){
                        response.createCallback()
                    } else {
                        response.objectIdCallback(id);
                    }
                }
            });
    }
}



