import {Component, OnInit, Input} from '@angular/core';
import {Router} from "@angular/router";
import {Link} from "../link";


/**
 * Clase ToolbarComponent. Implementa la funcionalidad de un Componente.
 *
 * ToolbarComponent es un componente que muestra la barra superior de la
 * aplicacin y se encarga de ofrecer los links que permiten la navegacin
 * por el sitio web.
 */
@Component({
    selector: 'app-toolbar',
    templateUrl: './toolbar.component.html',
    styleUrls: ['./toolbar.component.css']
})
export class ToolbarComponent implements OnInit {


    /**
     * Atributo links.
     *
     * El tipo es Array<Link>.
     */
    private _links: Array<Link>;


    /**
     * Atributo title.
     *
     * El tipo es string.
     *
     * Su visibilidad debe ser protected debido a que es un parmetro de
     * entrada del componente.
     */
    @Input() protected title: string;


    /**
     * Constructor de la clase ToolbarComponent.
     *
     * @param router Servicio Router que permite la redireccin hacia otras
     * rutas dentro de la aplicacin.
     */
    constructor(private router: Router,) {
    }


    /**
     * Getter del atributo links.
     *
     * @returns {Array<Link>}
     */
    get links(): Array<Link> {
        return this._links;
    }


    /**
     * Setter del atributo links.
     *
     * @param value
     */
    set links(value: Array<Link>) {
        this._links = value;
    }


    /**
     * Funcion ngOnInit.
     *
     * Implementa la funcin de la interfaz OnInit
     */
    ngOnInit() {
        this.links = [
            new Link('Home', '/app/home'),
            new Link('Elections', '/app/elections'),
            new Link('Parties', '/app/parties'),
            new Link('Regions', '/app/regions'),
            new Link('Exit', '/')
        ]
    }


    /**
     * Funcin goTo.
     *
     * Redirige al usuario hacia la ruta indicada como parmetro.
     *
     * @param link Link que representa la ruta relativa hacia la cul se
     * pretende navegar.
     */
    goTo(link: Link): void {
        this.router.navigate([link.url]);
    }

}



